	module.exports = {"data":{"markdownRemark":{"html":"<p>Go to Dongling. Dongling is about 150 kilometers in Zunhua City, Hebei Province. All the way to the road is relatively smooth. The plains of North China are very different from the rural areas of the south of the Yangtze River. Here are endless fields, mountains in the distance, but no hilly areas, and mountains are distinct. It is very different from the water towns and mountains in the south. The housing is dominated by bungalows and large courtyards, while the south is dominated by buildings and small courtyards. The crops are mainly corn, etc. It is the main crop of this season, and it grows very well and is lush. Such a field gives a feeling of broadness and a good mood.</p>\n<p>The rural economy is active. Today is the day of the market, passing through several markets and large scales. It's just not standardized, there is no venue, it's on the roadside and on the hillside, there is a local flavor. There are a variety of products in the market, mainly agricultural products, and many industrial products. The goods of the city enter the countryside. Of course, it is not far from the center of the big city, not a backward area, and the economy is also developed.</p>\n<p>I saw the market here and remembered the vendors at Wujiaochang. The culture there is very similar to the market here. The main reason is that the vendors at Wujiaochang were run by farmers who came to the city. In addition, if management fails, it will happen that urban business culture affects them, or whether the rural market culture influences the city. This is probably a long-term process in China. Not only the general economic activities, but also the cultural and spiritual aspects.</p>\n<p>After three and a half hours, I arrived at Yutian. There are 10,000 people here, and the economy is developing very well. The output value of industry and agriculture is 3.4 billion, and agriculture only accounts for 800 million. I asked the secretary what the party branch in the countryside is. He said that it is basically combative. Only a few have no fighting power. This protest fully proves this point.</p>\n<p>Then go to Zunhua and go to Dongling. I visited the tombs of Qianlong and the Empress Dowager Cixi that have been opened. Dongling was built in 1661 and will be built successively. The five emperors of the Qing Dynasty were buried. The scale is grand and the momentum is not small. In 1927, the warlord Sun Dianying stole the two tombs. There are not many people here, they are quiet, and there is an atmosphere that induces people to care for the past. There are so many people in the tourist spot that people lose interest.</p>\n<p>Then go to Tangshan. The new town of Tangshan is a new moon. I still remember the 1976 earthquake, but now I have built a brand new city on the ground.</p>\n<p>Returning to Beidaihe in the evening, I saw the downtown lights shining and feeling a thousand.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0132.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0132.md","frontmatter":{"title":"August 7th Sunday"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0132.md absPath of file >>> MarkdownRemark"}}

